import React, {useState} from 'react';
import {setUserSession} from '../utils/Common';
import axios from 'axios';

function Login(props){
    const[Loading, setLoading] = useState(false)
    const [error, setError] = useState(null);
    const username = GetInputValue("");
    const password = GetInputValue("");

    const handleLogin = () =>{
        setLoading(true);
        axios.post("https://testjugg1.herokuapp.com/", {
            username: username.value, 
            password: password.value})
            .then(response => {
                console.log(response.data);
                if(response.data == "Usuario valido"){
                    setUserSession(username.value);
                   props.history.push('/dashboard'); 
                } else {
                    setError("Usuario Invalido");
                    setLoading(false);
                }
        }).catch();

    }

    

    return(
        <header className="head2">
            <label>PruebaMX</label>
            <div>
                <p className="p1">Usuario:</p>
                <input type="text" {...username} />
            </div>
            <div>
                <p>Contrase√±a:</p>
                <input type="password" {...password} /> <br/>
                <label>{error}</label>
            </div>
            <div className="boton">
            <input className="botonin" type="button" onClick={handleLogin} 
            value={Loading ? 'Cargando...' : 'Entrar' }
            disabled={Loading} />
            </div>
        </header>
    );
}

const GetInputValue = (initialValue) =>{
    const [value, setValue] = useState(initialValue);

    const handleChange = (e) =>{
        setValue(e.target.value)
    }

    return{
        value,
        onChange: handleChange
    }
} 

export default Login;